.liked-songs-link {
  position: relative;

  .station-name-header {
    position: absolute;
    bottom  : 30px;

    h3 {
      font-size    : 2rem;
      margin-bottom: 5px;
    }

    // margin-bottom: ;
  }

  .station-desc {
    font-size         : 1.1rem;
    position          : absolute;
    top               : 40%;
    transform         : translateY(-50%);
    display           : -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow          : hidden;
  }
}


.liked-songs-play-icon-container {
  visibility      : hidden;
  opacity         : 0;
  background-color: #1db954;
  color           : white;
  position        : absolute;
  width           : 3.5rem;
  height          : 3.5rem;
  border-radius   : 50%;
  bottom          : 20px;
  right           : 20px;
  transition      : visibility 0s, opacity 0.3s linear;

  &:hover {
    transform: scale(1.1);
  }

  // cursor    : default;
}