.profiler {
    position: fixed;
    top     : 20px;
    right   : 38px;
    z-index : 10;

    :hover {
        cursor: pointer;
    }

    h2 {
        transition: 0.5s;
    }

    .szh-menu {
        li {
            min-width     : 100px;
            height        : auto;
            padding       : 0;
            display       : flex;
            flex-direction: column;
            align-items   : center;
        }
    }


    button {
        display         : flex;
        padding         : 0;
        align-items     : center;
        border          : none;
        background-color: rgba(128, 128, 128, 0);

        //img {
        //    height       : 40px;
        //    border       : 1px gray solid;
        //    width        : 40px;
        //    border-radius: 50%;
        //    object-fit   : cover;
        //}

        &.user {
            img {
                border: 1px solid rgb(29, 185, 84);
            }
        }
    }

    h2::before {
        margin-right: 10px;
    }

    h2::after {
        font-size  : 1rem;
        margin-left: 10px;
    }



}

.create-playlist {
    .buttons {
        span {
            display: block !important;
            padding: 0 !important;

            button {
                position    : absolute;
                top         : 0;
                left        : 0;
                // font-size: 0.75rem;
                // padding  : 0.7rem;
                min-width   : 100% !important;
                min-height  : 100% !important;
                padding     : 0 !important;
                opacity     : 0;
                cursor      : pointer;
            }
        }
    }
}

