.stations-container-new {
  padding-left : 1.6rem;
  padding-right: 1.6rem;

  .stations-header {
    padding-top  : 3rem;
    margin-bottom: 1rem;

    @media screen and (max-width: 760px) {
      padding-top: 6.5rem;
    }
  }

  .stations-cards {
    display              : grid;
    grid-template-columns: repeat(auto-fill, 200px);
    gap                  : 1rem;

    @include for-mobile-layout {
      grid-template-columns: repeat(auto-fill, 150px);
    }


    .station-preview {
      @include for-mobile-layout {
        width: 150px;
      }
    }

    .liked-songs-link {
      position   : relative;
      display    : block;
      grid-column: 1 / span 2;
      max-width  : 100%;
      background : linear-gradient(149.46deg, #450af5, #8e8ee5 99.16%);

      @media screen and (max-width:870px) {
        grid-column: unset;
        min-height : 250px;

      }


      &:hover {
        // background-color: #282828;

        .liked-songs-play-icon-container {
          visibility: visible;
          opacity   : 1;
          cursor    : pointer;
        }
      }
    }

  }
}



















.stations-container {
  width        : 100%;
  padding-top  : 3rem;
  margin-top   : 6rem;
  padding-left : 1.5rem;
  padding-right: 1.5rem;

  header {
    margin-bottom: 1rem;
  }
}

.stations-wrapper {
  width            : 100%;
  // padding-left  : 2rem;
  // padding-right : 2rem;
  padding-top      : 3rem;
  padding-bottom   : 120px;
}

.card.stations .genre {
  overflow             : hidden;
  grid-gap             : 5px;
  gap                  : 5px;
  margin-bottom        : 20px;
  width                : 100%;
  display              : grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  float                : left;

  @include for-mobile-layout {
    grid-template-columns: repeat(auto-fit, minmax((150px), 1fr));
  }
}